import React from 'react';
import { Document, Page} from 'react-pdf';
import examplePDF from '../pdfs/Resume.pdf';


function PDFs() {
    return (
        <div>
        <a href={examplePDF} download>Download PDF</a>

      <Document url={examplePDF}>
        <Page pageNumber={1} />
      </Document>
        </div>
    );
}

export default PDFs;
